import { Component, OnInit, Input } from '@angular/core';
import { Sight } from 'src/app/app.component';

@Component({
  selector: 'app-placemark',
  templateUrl: './placemark.component.html',
  styleUrls: ['./placemark.component.css']
})
export class PlacemarkComponent implements OnInit {

  @Input() sight : Sight;
  @Input() idx : number;
  onCar = false;

  constructor() { }

  ngOnInit() {
    this.onCar = false;
    if (this.sight.time > '30') {
      this.sight.time = (Math.round(+this.sight.time/8)).toString()
      this.onCar = true;
    }
  }

}
